import React from 'react';

const Controls = ({ next, prev, disabledPrev, disabledNext }) => (
  <section className="controls">
    <button
      type="button"
      className="button"
      onClick={prev}
      disabled={disabledPrev}
    >
      Назад
    </button>
    <button
      type="button"
      className="button"
      onClick={next}
      disabled={disabledNext}
    >
      Вперед
    </button>
  </section>
);

export default Controls;
